<template>
  <div>
    <table>
      <tr><th colspan="3">Skills</th></tr>
      <tr v-for="n in skillsArray.length" :key="n">
        <td v-for="skill in skillsArray[n-1]" :key="skill.name">
          <button @click="rollSkill(skill)" class="btn btn-sm btn-primary">{{skill.name}} <span v-if="getSkillMod(skill)>=0">+</span>{{getSkillMod(skill)}}</button>
        </td>
      </tr>
    </table>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex';
export default {
  computed: {
    ...mapGetters({
      getSkillMod: "getSkillMod",
      character: "getCharacter"
    }),
    skillsArray () {
      let count = 0;
      let rowcount = 0;
      let skillsArray = [];
      console.log(this.character);
      this.character.skills.forEach((skill) => {
        if (count % 3 === 0) {
          rowcount++;
          count++;
          skillsArray.push([skill]);
        } else {
          skillsArray[rowcount-1].push(skill);
          count++;
        }
      });
      return skillsArray;
    }
  },
  methods: {
    ...mapActions({
      sendroll: "sendroll"
    }),
    buildtemplate (rollname, keys) {
      let template = "&{template:default}{{name=" + rollname + "}}";
      for (let key in keys) {
        template += "{{" + key + "=" + keys[key] + "}}";
      }
      return template;
    },
    rollSkill(skill) {
      console.log(skill);
      let mod = this.getSkillMod(skill);
      let rolltext = { Skill: "[[1d20+" + mod + "]] [[1d20+" + mod + "]]" };
      this.sendroll(this.buildtemplate(skill.name, rolltext));
    }
  }
};
</script>
