<template>
  <div>
    <h5>
      Initiative (<span v-if="getInitMod>=0">+</span>{{ getInitMod }})
      <button class="btn btn-primary btn-sm" @click="init(0)">Normal</button>&nbsp;
      <button class="btn btn-primary btn-sm" @click="init(2)">Advantage</button>&nbsp;
      <button class="btn btn-primary btn-sm" @click="init(1)">Disadvantage</button>
    </h5>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  computed: {
    ...mapGetters({
      character: "getCharacter",
      getInitMod: "getInitMod"
    })
  },
  methods: {
    ...mapActions({
      sendroll: "sendroll"
    }),
    buildtemplate (rollname, keys) {
      let template = "&{template:default}{{name=" + rollname + "}}";
      for (let key in keys) {
        template += "{{" + key + "=" + keys[key] + "}}";
      }
      return template;
    },
    init (type) {
      let base = "[[1d20+";
      if (type) {
        base = "[[2d20dh1+";
        type -= 1;
      }
      if (type) {
        base = "[[2d20dl1+";
      }
      base += this.getInitMod + " &{tracker}]]";
      let keys = { "Initiative": base };
      this.sendroll(this.buildtemplate("Initiative", keys));
    }
  }
};
</script>
