<template>
  <div>
    <h5>Other Rolls</h5>
      <label class="form-inline">Add Levels: <input type="number" v-model="boostlevels" class="form-control" /></label>
    <div v-for="(roll, index) in character.rolls" :key="index" style="margin-top:2px;">
      <button @click="rollit(roll)" class="btn btn-sm btn-primary">{{roll.name}}</button>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  computed: {
    ...mapGetters({
      character: 'getCharacter',
      getSaveDC: 'getSaveDC',
      getDamageBonus: 'getDamageBonus'
    })
  },
  data: function () {
    return { boostlevels: 0 };
  },
  methods: {
    ...mapActions({
      sendroll: "sendroll"
    }),
    buildtemplate (rollname, keys) {
      let template = "&{template:default}{{name=" + rollname + "}}";
      for (let key in keys) {
        template += "{{" + key + "=" + keys[key] + "}}";
      }
      return template;
    },
    rollit (roll) {
      let keys = {};
      if (roll.savestat) {
        keys["save"] = "DC " + this.getSaveDC(roll.stat) + " " + roll.savestat + " Save";
        keys["save for"] = roll.savefor;
      }
      roll.damage.forEach((d) => {
        let droll = "[[" + d.dice + "+" + this.getDamageBonus(d, roll.stat) + "]]";
        let upcastdice = "[[";
        for (var x = 0; x < this.boostlevels; x++) {
          upcastdice += d.higherlevels + "+";
        }
        upcastdice += "0]]";
        droll += " " + upcastdice; 
        keys[d.label] = droll;
      });
      keys["range"] = roll.range;
      keys["description"] = roll.description;
      this.sendroll(this.buildtemplate(roll.name, keys));
    }
  }
};

</script>
