<template>
  <div>
    <label class="form-inline">
      Enter Character ID
      <input v-model="charid" type="text" class="form-control" />
    </label>&nbsp;
    <button @click="loadCharacter(charid)" class="btn btn-sm btn-primary">Go Get Em!</button>
    <div v-if="character.name.length > 0">
      <h4>{{ character.name }}</h4>
      <div class="row">
        <div class="col-12">
          <initiative />
        </div>
      </div>
      <div class="row">
        <div class="col-3">
          <saves />
        </div>
        <div class="col-9">
          <skills />
        </div>
      </div>
      <div class="row">
        <div class="col-6">
          <attacks />
        </div>
        <div class="col-6">
          <otherrolls />
        </div>
      </div>
      <div class="row">
        <div class="col-12">
          <spells />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
/* global chrome browser */
import  { mapState, mapActions } from 'vuex';
import saves from './saves';
import skills from './skills';
import attacks from './attacks';
import spells from './spells';
import initiative from './initiative';
import otherrolls from './otherrolls';

export default {
  components: {
    initiative,
    otherrolls,
    saves,
    skills,
    attacks,
    spells
  },
  computed: {
    ...mapState({
      character: "currentCharacter"
    })
  },
  data () {
    return { charid: "" };
  },
  methods: {
    ...mapActions({
      loadCharacter: "loadCharacter"
    })
  },
  mounted () {
    let instance = this;
    if (chrome) {
      chrome.storage.sync.get({ elthelascharacter: false }, function (val) {
        if (val.elthelascharacter) {
          console.log(val.elthelascharacter);
          instance.loadCharacter(val.elthelascharacter);
        }
      });
    } else {
      browser.storage.sync.get({ elthelascharacter: false }, function (val) {
        if (val.elthelascharacer) {
          console.log(val.elthelascharacer);
          instance.loadCharacter(val.elthelascharacter);
        }
      });
    }
  }
};
</script>
