<template>
  <div>
    <h5>Spells</h5>
    <div class="btn-group print-hide smalltext">
      <button class="btn btn-sm btn-primary" @click="displayLevel = 'cantrip'">cantrip</button>
      <button class="btn btn-sm btn-primary" @click="displayLevel = 'level1'">1</button>
      <button class="btn btn-sm btn-primary" @click="displayLevel = 'level2'">2</button>
      <button class="btn btn-sm btn-primary" @click="displayLevel = 'level3'">3</button>
      <button class="btn btn-sm btn-primary" @click="displayLevel = 'level4'">4</button>
      <button class="btn btn-sm btn-primary" @click="displayLevel = 'level5'">5</button>
      <button class="btn btn-sm btn-primary" @click="displayLevel = 'level6'">6</button>
      <button class="btn btn-sm btn-primary" @click="displayLevel = 'level7'">7</button>
      <button class="btn btn-sm btn-primary" @click="displayLevel = 'level8'">8</button>
      <button class="btn btn-sm btn-primary" @click="displayLevel = 'level9'">9</button>
    </div>
    <span class="smalltext print-hide"><input type="checkbox" v-model="preparedonly" />Prepared Only</span><br />
    <table class="table table-sm">
      <thead><tr><th>Spell</th><th>Casting Time</th><th>Duration</th><th>Class</th><th colspan="2">-</th></tr></thead>
      <tbody>
        <tr v-for="spell in sortedSpells" :key="spell.title">
          <td>
            <button class="btn btn-sm btn-primary" @click="rollspell(spell)">{{ spell.title }}<span v-if="spell.ritual"> (ritual)</span></button>
          </td>
          <td>{{ spell.castingTime }}</td>
          <td>{{ spell.duration }}</td>
          <td>{{ spell.class }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex';
export default {
  setup() {
    
  },
  computed: {
    ...mapGetters({
      character: "getCharacter",
      getSaveDC: "getSaveDC"
    }),
    sortedSpells() {
      let spells = this.character.spells[this.displayLevel].filter((spell) => {
        if ((spell.prepared && this.preparedonly) || !this.preparedonly || this.displayLevel === 'cantrip') {
          return true;
        } else {
          return false;
        }
      });
      var x = spells.slice(0);
      x.sort((b, c) => {
        if (b.title > c.title) {
          return 1;
        } else if (c.title > b.title) {
          return -1;
        }
        return 0;
      });
      return x;      
    }
  },
  data () {
    return {
      displayLevel: "cantrip",
      preparedonly: false
    };
  },
  methods: {
    ...mapActions({
      sendroll: "sendroll"
    }),
    rollspell (spell) {
      let v = spell.components.includes("V") ? 1 : 0;
      let s = spell.components.includes("S") ? 1 : 0;
      let text = "&{template:spell} {{level=" + spell.school + " " + spell.level + "}} {{name=" +
        spell.title + "}} {{castingtime=" + spell.castingTime + "}} {{range=" + spell.range + "}} {{target=see description}} {{v=" +
        v + "}} {{s=" + s + "}} 0 {{material=not enabled}} {{duration=" + spell.duration + "}} {{description=" + spell.description +
        "}} {{athigherlevels=}} 0 {{innate=}} {{savedc=}}";
      this.sendroll(text);
    },
  }
};
</script>