<template>
  <div>
    <h5>Attacks</h5>
    <div v-for="(attack, index) in character.attacks" :key="index" style="margin-top: 2px;">
      <button @click="rollAttack(attack)" class="btn btn-sm btn-primary">{{attack.name}} <span v-if="getAttackBonus(attack)>=0">+</span>{{getAttackBonus(attack)}}</button>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  computed: {
    ...mapGetters({
      character: "getCharacter",
      getAttackBonus: "getAttackBonus",
      getDamageBonus: "getDamageBonus"
    })
  },
  methods: {
    ...mapActions({
      sendroll: "sendroll"
    }),
    buildtemplate (rollname, keys) {
      let template = "&{template:default}{{name=" + rollname + "}}";
      for (let key in keys) {
        template += "{{" + key + "=" + keys[key] + "}}";
      }
      return template;
    },
    rollAttack (attack) {
      let base = "[[1d20cs>" + attack.critRange + "+" + this.getAttackBonus(attack) + "]]";
      let attrolls = base + " " + base;
      let eaccuracy = this.character.feats.reduce((a, b) => {
        return (b.name === "Elven Accuracy") || a;
      }, false) && (attack.stat === 1 || attack.stat === 3 || attack.stat === 4 || attack.stat === 5);
      if (eaccuracy) {
        attrolls += " " + base;
      }
      let keys = {"Attack Roll": attrolls};
      attack.damage.forEach((d) => {
        let droll = "[[" + d.dice + "+" + this.getDamageBonus(d, attack.stat) + "]] crit: [[" + d.dice + "]]";
        keys[d.dtype] = droll;
      });
      this.sendroll(this.buildtemplate(attack.name, keys));
    }
  }
}
</script>
