<template>
  <div>
    <table>
      <thead>
        <tr>
          <th>Saves</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td><button @click="rollSave(0)" class="btn btn-sm btn-primary">STR <span v-if="getSaveMod(0)>=0">+</span>{{getSaveMod(0)}}</button></td>
        </tr>
        <tr>
          <td><button @click="rollSave(1)" class="btn btn-sm btn-primary">DEX <span v-if="getSaveMod(1)>=0">+</span>{{getSaveMod(1)}}</button></td>
        </tr>
        <tr>
          <td><button @click="rollSave(2)" class="btn btn-sm btn-primary">CON <span v-if="getSaveMod(2)>=0">+</span>{{getSaveMod(2)}}</button></td>
        </tr>
        <tr>
          <td><button @click="rollSave(3)" class="btn btn-sm btn-primary">INT <span v-if="getSaveMod(3)>=0">+</span>{{getSaveMod(3)}}</button></td>
        </tr>
        <tr>
          <td><button @click="rollSave(4)" class="btn btn-sm btn-primary">WIS <span v-if="getSaveMod(4)>=0">+</span>{{getSaveMod(4)}}</button></td>
        </tr>
        <tr>
          <td><button @click="rollSave(5)" class="btn btn-sm btn-primary">CHA <span v-if="getSaveMod(5)>=0">+</span>{{getSaveMod(5)}}</button></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex';
export default {
  computed: {
    ...mapGetters({
      getSaveMod: "getSaveMod"
    })
  },
  methods: {
    ...mapActions({
      sendroll: "sendroll"
    }),
  buildtemplate (rollname, keys) {
    let template = "&{template:default}{{name=" + rollname + "}}";
    for (let key in keys) {
      template += "{{" + key + "=" + keys[key] + "}}";
    }
    return template;
  },
    rollSave(stat) {
      let mod = this.getSaveMod(stat);
      let statname = ["Strength", "Dexterity", "Constitution", "Intelligence", "Wisdom", "Charisma"][stat];
      let rolltext = { Save: "[[1d20+" + mod + "]] [[1d20+" + mod + "]]" };
      this.sendroll(this.buildtemplate(statname + " Saving Throw", rolltext));
    }
  }
}
</script>
