/* global chrome */
import { createStore } from 'vuex';
import axios from 'axios';
import getters from './getters';
import character from './classes/character';

export const store = createStore({
  state () {
    return {
      currentCharacter: character()
    };
  },
  getters: getters,
  actions: {
    loadCharacter({ state }, charid) {
      axios.get("https://elthelas.com/characters/" + charid).then(function (response) {
        state.currentCharacter = response.data;
        if (chrome) {
          chrome.storage.sync.set({ "elthelascharacter": charid }, function() {
            console.log('Value is set to ' + charid);
          });
        } else {
          browser.storage.sync.set({ "elthelascharacter": charid }, function () {
            console.log("Value is set to: ", charid);
          });
        }
      }).catch((e) => {
        console.log(e);
      });
    },
    sendroll (store, roll) {
      chrome.tabs.query({active: true, currentWindow: true}, function(tabs) {
        var activeTab = tabs[0];
        console.log(roll);
        if (chrome) {
          chrome.tabs.sendMessage(activeTab.id, { "roll": roll });
        } else {
          browser.tabs.sendMessage(activeTab.id, { "roll": roll });
        }
      });
    },
  }
});
